<template>
<div class="home-hero">
   <v-container fill-height >
       <v-layout align-center justify-center>
           <v-flex xs12 sm8 md4>
               <v-card class="elevation-12">
                   <v-toolbar dark  class="brown lighten-1">
                       <v-toolbar-title>Join us</v-toolbar-title>
                   </v-toolbar>
                   <v-card-text>
                       <v-form ref="form" v-model="valid" lazy-validation>
                           <v-text-field prepend-icon="person" name="email" label="Email" type="email"
                           v-model="email" :rules="emailRules" required>

                           </v-text-field>
                           <v-text-field prepend-icon="lock" name="password" label="Password" type="password"
                           id="password" required v-model="password" :rules="passwordRules">

                           </v-text-field>
                       </v-form>

                       <v-card-actions>
                           <v-spacer></v-spacer>
                           <v-btn  class="brown lighten-2" :disabled="!valid" @click="submit">
                            Join</v-btn>
                       </v-card-actions>
                   </v-card-text>
               </v-card>

           </v-flex>
       </v-layout>
   </v-container>
   </div>
</template>

<script>
export default {
    name: 'Join',
    data(){
        return{
        valid: false,
        email: '',
        password: '',
        emailRules: [
            v => !!v || 'Email is required',
            v => /.+@.+/.test(v) || 'Email must be valid'
        ],
        passwordRules: [
            v => !!v || 'Password is required',
            v => v.length >= 6 || 'Password must be greater than 6 characters'
        ]
        };
    },
    methods: {
        submit() {
            if (this.$refs.form.validate()) {
                this.$store.dispatch('userJoin', {
                    email: this.email,
                    password: this.password
                });
            }
        }
    }

};
</script>

<style scoped>
.home-hero {
    background-image: url('http://source.unsplash.com/0BhSKStVtdM');
    background-size: cover;
    width: 100%;
    height: 100%;
}
</style>